.oui-input,
.oui-input.oui-calendar__control,
.oui-select__input,
.oui-textarea,
.oui-ui-select_single .ui-select-match,
.oui-ui-select_single .ui-select-search,
.oui-ui-select_multiple .ui-select-match-container {
  border-width: 0 0 2px 0;
}

.oui-textarea__footer {
  border: none;
}

.oui-input-group:not(.oui-input-group_button) .oui-button {
  border-radius: @oui-global-base-radius;
}
